import { EditorView, NodeView } from 'prosemirror-view';
import { Node } from 'prosemirror-model';
import { Plugin } from 'prosemirror-state';
import { ImageState } from './state';

const MIN_WIDTH = 10;

export const createImageViewPlugin = () => {
    return new Plugin({
        props: {
            nodeViews: {
                image: (node, view, getPos) => {
                    if (node.attrs.state === ImageState.LOADING) {
                        return new LoadingImageView(node, view, getPos);
                    } else if (node.attrs.state === ImageState.SUCCESS) {
                        return new ResizableImageView(node, view, getPos);
                    } else if (node.attrs.state === ImageState.FAILED) {
                        return new FailedImageView(node, view, getPos);
                    }
                },
            },
        },
    });
};

class FailedImageView implements NodeView {
    node: Node;
    view: EditorView;
    getPos: () => number;
    dom: HTMLDivElement;

    public constructor(node: Node, view: EditorView, getPos: () => number) {
        this.node = node;
        this.view = view;
        this.getPos = getPos;

        const wrapper = document.createElement('div');
        wrapper.className = `image-wrapper state-${node.attrs.state}`;
        wrapper.setAttribute('aria-live', 'polite');
        wrapper.setAttribute(
            'aria-busy',
            node.attrs.state === ImageState.LOADING ? 'true' : 'false',
        );

        const errorElement = document.createElement('span');
        errorElement.className = 'image-error';
        errorElement.innerText = `⚠️ Failed to upload '${node.attrs.alt}'`;

        wrapper.appendChild(errorElement);

        this.dom = wrapper;
    }
}

class LoadingImageView implements NodeView {
    node: Node;
    view: EditorView;
    getPos: () => number;
    dom: HTMLDivElement;

    public constructor(node: Node, view: EditorView, getPos: () => number) {
        this.node = node;
        this.view = view;
        this.getPos = getPos;

        const wrapper = document.createElement('div');
        wrapper.className = `image-wrapper state-${node.attrs.state}`;
        wrapper.setAttribute('aria-live', 'polite');
        wrapper.setAttribute(
            'aria-busy',
            node.attrs.state === ImageState.LOADING ? 'true' : 'false',
        );

        const img = document.createElement('img');
        img.src = node.attrs.src;
        img.alt = node.attrs.alt;
        img.style.maxWidth = node.attrs.maxWidth;

        const spinnerElement = document.createElement('limel-linear-progress');
        spinnerElement.setAttribute('indeterminate', 'true');

        wrapper.appendChild(img);
        wrapper.appendChild(spinnerElement);

        this.dom = wrapper;
    }
}

class ResizableImageView implements NodeView {
    node: Node;
    view: EditorView;
    getPos: () => number;
    dom: HTMLDivElement;
    img: HTMLImageElement;

    public constructor(node: Node, view: EditorView, getPos: () => number) {
        this.node = node;
        this.view = view;
        this.getPos = getPos;

        this.dom = document.createElement('div');
        this.dom.className = 'resize-wrapper';

        this.img = document.createElement('img');
        this.img.src = node.attrs.src;
        this.img.alt = node.attrs.alt;
        this.img.style.maxWidth = node.attrs.maxWidth;

        const createResizeHandle = (position: 'bottom-right' | 'top-left') => {
            const handle = document.createElement('div');
            handle.className = `resize-handle ${position}`;
            handle.setAttribute('role', 'slider');
            handle.setAttribute(
                'aria-label',
                'Drag to right or left to resize the image',
            );
            handle.setAttribute('tabindex', '0');
            handle.setAttribute('aria-valuemin', MIN_WIDTH.toString());
            handle.setAttribute(
                'aria-valuenow',
                this.img.offsetWidth.toString(),
            );
            handle.setAttribute(
                'aria-valuetext',
                `${this.img.offsetWidth} pixels`,
            );
            handle.setAttribute('aria-grabbed', 'false');

            handle.addEventListener('mousedown', (e) => {
                handle.setAttribute('aria-grabbed', 'true');
                this.onResizeStart(e, position);
            });

            return handle;
        };

        const bottomRightHandle = createResizeHandle('bottom-right');
        const topLeftHandle = createResizeHandle('top-left');

        this.dom.appendChild(this.img);
        this.dom.appendChild(bottomRightHandle);
        this.dom.appendChild(topLeftHandle);
    }

    private onResizeStart = (
        event: MouseEvent,
        position: 'bottom-right' | 'top-left',
    ) => {
        event.preventDefault();
        const handle = event.target as HTMLElement;

        const startX = event.clientX;
        const startWidth = this.img.offsetWidth;

        const onMouseMove = (e: MouseEvent) => {
            const delta = e.clientX - startX;
            const widthDelta = position === 'top-left' ? -delta : delta;
            const newWidth = Math.max(MIN_WIDTH, startWidth + widthDelta);

            this.img.style.width = `${newWidth}px`;

            const handles = this.dom.querySelectorAll('.resize-handle');
            handles.forEach((resizeHandle) => {
                resizeHandle.setAttribute('aria-valuenow', newWidth.toString());
                resizeHandle.setAttribute(
                    'aria-valuetext',
                    `${newWidth} pixels`,
                );
            });
        };

        const onMouseUp = () => {
            window.removeEventListener('mousemove', onMouseMove);
            window.removeEventListener('mouseup', onMouseUp);
            handle.setAttribute('aria-grabbed', 'false');

            this.view.dispatch(
                this.view.state.tr.setNodeMarkup(this.getPos(), undefined, {
                    ...this.node.attrs,
                    width: this.img.style.width,
                    height: this.node.attrs.height,
                }),
            );
        };

        window.addEventListener('mousemove', onMouseMove);
        window.addEventListener('mouseup', onMouseUp);
    };
}
