import { NodeSpec } from 'prosemirror-model';
import { Node } from 'prosemirror-model';
import { ImageState } from './state';

export function getImageNode(): Record<string, NodeSpec> {
    return { image: createImageNodeSpec() };
}

export function createImageNodeSpec(): NodeSpec {
    return {
        group: 'inline',
        inline: true,
        attrs: {
            src: { default: '' },
            alt: { default: '' },
            fileInfoId: { default: '' },
            maxWidth: { default: '100%' },
            state: { default: '' },
        },
        toDOM(node) {
            if (node.attrs.state === ImageState.FAILED) {
                // TODO: Fix this depending on how we visualize fail state
                // We should probably remove FAILED state
                // since we are not rendering an image?
                return '';
            } else {
                return createImageElement(node);
            }
        },
        parseDOM: [
            {
                tag: 'img',
                getAttrs(dom) {
                    return {
                        src: dom.getAttribute('src') || '',
                        alt: dom.getAttribute('alt') || '',
                        maxWidth: dom.style.maxWidth || '100%',
                        state: ImageState.SUCCESS,
                    };
                },
            },
        ],
    };
}

function createImageElement(node: Node): HTMLElement {
    const img = document.createElement('img');
    img.src = node.attrs.src;
    img.alt = node.attrs.alt;
    img.style.maxWidth = node.attrs.maxWidth;
    return img;
}
